body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


main {
	margin: 0 auto;
	max-width: 90%;
	padding: 50px 30px;
}

:root {
	--primary: #055024;
	--dark: #A5ACAF;
	--quaternary: #2e813f;
	--quinary: #bfd7ea;
	--secondary: #002244;
	--light: #2e813f;
	--error: rgb(228, 46, 1);
	--spacing-one: calc(1rem);
	--spacing-two: calc(1rem);
	--spacing-five: calc(2.5rem);
}

* {
	box-sizing: border-box;
}

html,
body {
	min-height: 100%;
}

body {
	margin: 0;
	padding: 0;
	font-weight: 400;
	line-height: 1.5;
	font-size: 14px;
	letter-spacing: .1em;
	background: var(--secondary);
	color: var(--light);
	font-family: 'Arial', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Arial', sans-serif;
	font-weight: 500;
	margin: 5px 0;
	line-height: 1.25;
}

p {
	font-size: 1.1rem;
	margin-top: 0;
}

a {
	color: #A5ACAF;
	text-decoration: none;
	font-weight: bold;
	border-radius: 5px;
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
}



header {
	background: var(--secondary);
	display: flex;
	justify-content: space-between;
	padding: .75rem;
	text-align: center;
}


header a {
	color: var(--light);
}

button {
	background: var(--quaternary);
	border: 0;
	border-radius: .5rem;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	padding: 10px 15px;
	font-size: 1rem;

}

button:hover {
	opacity: .8;
}

button[disabled] {
	cursor: default;
	opacity: .8;
}

.footer {
  background: var(--secondary);
	text-align: center;
}

#div1, #div2 {
  display:inline;
}

#div3 {font-size: 9px}

.about,
.contact,
.resume {
  padding-left: 30px;
  padding-bottom: 12px;
  background-color:#dcf0f8 ;
}

img {
	max-width: 100%;
}

span[role="img"] {
	cursor: default;
}

form {
	width: 40%;
}

input {
	font-size: 1rem;
	padding: 0.25rem;
	border: 1px solid var(--dark);
	outline: none;
	border-radius: 3px;
	transition: box-shadow 0.2s;
	width: 600px;
}

input:focus {
	border: 1px solid var(--secondary);
	box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

ol li {
	list-style: inside;
}

ul li {
	list-style: none;
}

nav a:hover {
	text-decoration: none;
}

nav li {
	cursor: pointer;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.container {
	width: 85%;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
}

.flex-row {
	display: flex;
	justify-content: space-between;
  
}

li {
  margin-right: 16px;
}
.space-between {
	justify-content: space-between;
}

.flex-end {
	justify-content: flex-end;
}

.navActive {
	color: var(--primary);
}

.photo {
	width: auto;
	height: auto;
	max-width: 480px;
	max-height: 640px;
	margin-bottom: 50px;
}

.image {
	width: auto;
	height: auto;
	max-width: 500px;
	max-height: 500px;
	padding: 50px;
}

.logo {
	width: 50px;
	height: 50px;
	margin-bottom: 50px;
	cursor: pointer;
}

.logo:hover {
	opacity: 0.5;
}

.center {
	display: flex;
	justify-content: center;
}

.page-header {
	font-family: 'Roboto', sans-serif;
	font-size: 2.5em;
	font-weight: 500;
	margin: 5px 0;
	line-height: 1.25;
	padding-bottom: 2rem;
}

.bottom-spacing {
	margin-bottom: var(--spacing-five);
}

.card {
	width: 33%;
	text-align: center;
}

.card-title {
	font-size: 1.5rem;
	color: var(--quaternary);
}

.card-subtitle {
	font-size: 1rem;
	color: var(--tertiary);
}

.card-text {
	font-size: 0.75rem;
	color: var(--quinary);
}

.card-techs {
	font-size: 0.75rem;
	height: 4rem;
	color: var(--quinary);
}

.card-image {
	max-width: 300px;
	max-height: 300px;
}

.card-link {
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	font-size: 0.70rem;
}

.padding {
	padding: 10px;
}

@media screen and (max-width: 980px) {}

@media screen and (max-width: 817px) {
	header div {
		position: relative;
		width: 110%;
	}

	header div h2 {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	h2 {
		font-size: 1.5rem;
	}

	nav ul li span {
		font-size: 0.75em;
	}

	.mobile-header {
		width: 110%;
	}

	.mobile-row {
		display: block;
		margin: 0 100px 0 auto;
	}

	.mobile-view {
		display: inline;
		text-align: end;
	}

	.photo {
		width: auto;
		height: auto;
		max-width: 500px;
		max-height: 300px;
		padding: 50px;
	}

	input {
		width: 350px;
	}

	textarea {
		width: 350px;
		height: 120px;
		border: 3px solid #cccccc;
		padding: 5px;
	}

	ol {
		font-size: 0.75rem;
	}
}

@media screen and (max-width: 768px) {}

@media screen and (max-width: 575px) {}
